import {  Component } from '@angular/core';
import {  NavController, NavParams } from 'ionic-angular';
import {  Destinos } from  '../../../providers/models/destinos';
import {  DestinosDataProvider } from '../../../providers/models/destinos-data';
@Component({
  selector: 'page-explorar',
  templateUrl: 'explorar.html',
})
export class ExplorarPage {
  destinos: Destinos[] = [];

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public service: DestinosDataProvider) {
    this.destinos = service.data;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ExplorarPage');
  }
  Reserve(){
  }
}



